	overflow-y: scroll;
} */

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

a,
a span {
	color: black;
}

ul.menu a:hover,
a.underline,
.underline {
	color: black;
	text-decoration: underline;
}

.button {
	background-color: rebeccapurple;
	margin-bottom: 0;
}

.button.a {
	color: black;
	background: transparent;
	padding-top: 0;
	padding-bottom: 0;
}

button.border-black {
	border-color: black;
}

hr {
	max-width: 100vw;
	margin: 0;
}

.small-img {
	width: 150px;
}

.small-div {
	max-width: 400px;
	margin-top: 10px;
	margin-bottom: 10px;
}

.medium-div {
	max-width: 600px;
}

.loose-border {
	border-radius: 10px;
	border: 1px solid black;
}

.large-div {
	max-width: 1100px;
}

.rounded-callout {
	padding: 0;
	border-radius: 10px;
	overflow: hidden;
}

div.orbit-image {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: contain;
	height: 200px;
}

button.orbit-previous,
button.orbit-next {
	color: black;
}

button.orbit-previous:hover,
button.orbit-next:hover {
	color: white;
}

.truncate-wrapper {
	display: grid;
}

.pre-line {
	white-space: pre-line;
}

.normal {
	white-space: normal;
}

hr {
	margin: 0;
}

.p {
	display: block;
}

.main-background {
	background: #faf9f5;
}

.sub-background {
	background: #f8ebe6;
}

.optional-background {
	background: #fbfbfa;
}

.img-with-shadow:hover {
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.thumbnail {
	margin: 0;
}

.text-red {
	color: orangered;
}

ul .menu {
	background: inherit;
}

.text-green {
	color: green;
}

.text-black {
	color: black;
}

li.tabs-title a,
li.tabs-title.is-active a {
	color: inherit;
}

.padding-0 {
	padding: 0;
}

.medal {
	color: yellow;
	background: rgb(0, 0, 104);
	padding: 10px;
	border-radius: 20px;
}
